<template>
<transition name="list-fade">
  <div class="playlist" v-show="showFlag" @click="hide">
    <!--父级绑定了监听事件，如果不要让子元素点击事件冒泡到父元素，需要阻止-->
    <div class="list-wrapper" @click.stop>
      <div class="list-header">
        <h1 class="title">
          <i class="icon" :class="iconMode" @click="changeMode"></i>
          <span class="text">{{modeText}}</span>
          <span class="clear" @click.stop="showConfirm"><i class="icon-clear"></i></span>
        </h1>
      </div>
      <scroll ref="listContent" class="list-content" :data="sequenceList">
        <!--使用transition-group执行动画，tag映射为指定标签-->
        <!--由于动画执行需要时间（约100ms），而滚动组件监听数据变化是20ms，所以需要定制变化时间，使得滚动高度正确-->
        <transition-group name="list" tag="ul" :refreshDelay="refreshDelay">
          <li :key="item.id" ref="listItem" class="item" v-for="(item,index) in sequenceList" @click.stop="selectItem(item,index)">
            <i class="current" :class="getCurrentIcon(item)"></i>
            <span class="text">{{item.name}}</span>
            <span @click.stop="toggleFavorite(item)" class="like">
              <i :class="getFavoriteIcon(item)"></i>
            </span>
            <span class="delete" @click.stop="deleteOneSong(item)">
              <i class="icon-delete"></i>
            </span>
          </li>
        </transition-group>
      </scroll>
      <div class="list-operate">
        <div class="add" @click.stop="addSong">
          <i class="icon-add"></i>
          <span class="text">添加歌曲到队列</span>
        </div>
      </div>
      <div class="list-close"  @click.stop="hide">
        <span>关闭</span>
      </div>
    </div>
    <confirm @confirm="confirmClear" ref="confirm" text="是否清空播放列表" confirmBtnText="清空"></confirm>
    <add-song ref="addSong"></add-song>
  </div>
</transition>
</template>

<script type="text/ecmascript-6">
  // 从vuex获取状态树数据
  import {mapGetters,mapMutations,mapActions} from 'vuex'
  import Scroll from 'base/scroll/scroll'
  import {playMode} from 'common/js/config'
  import Confirm from 'base/confirm/confirm'
  import {playerMixin} from 'common/js/mixin'
  import AddSong from 'components/add-song/add-song'
  export default {
    mixins:[playerMixin],
    data(){
      return {
        showFlag:false,
        refreshDelay:100
      }
    },
    computed:{
      modeText(){
        return this.mode === playMode.sequence ? '顺序播放' : this.mode === playMode.random ? '随机播放' : '单曲循环'
      }
   /*   ...mapGetters([
        'sequenceList',
        'currentSong',
        'playlist',
        'mode'
      ])*/
    },
    methods:{
      addSong(){
        this.$refs.addSong.show()
      },
      show(){
        this.showFlag = true
        // 列表切换到显示，才开始真正计算滚动的dom，否则滚动无效
        setTimeout(()=>{
          this.$refs.listContent.refresh()
          this.scrollToCurrent(this.currentSong)
        },20)
      },
      hide(){
        this.showFlag = false
      },
      showConfirm(){
        this.$refs.confirm.show()
      },
      confirmClear(){
        this.deleteSongList()
        this.hide()
      },
      getCurrentIcon(item){
        if(this.currentSong.id === item.id){
          return 'icon-play'
        }else{
          return ''
        }
      },
      selectItem(item,index){
        // 如果是随机播放
        if(this.mode === playMode.random){
          index = this.playlist.findIndex((song)=>{
            return song.id === item.id
          })
        }
        //如果是顺序播放
        this.setCurrentIndex(index)
        this.setPlayingState(true)
      },
      deleteOneSong(item){
        //bug 在搜索页搜索添加歌曲列表，当删除当前播放歌曲的时候，删除 执行了两次?
        this.deleteSong(item)
        // 如果删除完
        if(!this.playlist.length){
          this.hide()
        }
      },
      // 滚动到当前歌曲播放列表位置
      scrollToCurrent(current){
        const index = this.sequenceList.findIndex((song)=>{
          return song.id === current.id
        })
        this.$refs.listContent.scrollToElement(this.$refs.listItem[index],300)
      },
      ...mapMutations({
        /*'setCurrentIndex':'SET_CURRENT_INDEX',
        'setPlayingState':'SET_PLAYING_STATE'*/
      }),
      ...mapActions([
        'deleteSong',
        'deleteSongList'
      ])
    },
    watch:{
      currentSong(newSong,oldSong){
        if( !this.showFlag || newSong.id === oldSong ){
          return
        }
        setTimeout(() => {
          this.scrollToCurrent(newSong)
        }, 20)
      }
    },
    components:{
      Scroll,
      Confirm,
      AddSong
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .playlist
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 200
    background-color: $color-background-d
    &.list-fade-enter-active, &.list-fade-leave-active
      transition: opacity 0.3s
      .list-wrapper
        transition: all 0.3s
    &.list-fade-enter, &.list-fade-leave-to
      opacity: 0
      .list-wrapper
        transform: translate3d(0, 100%, 0)
    &.list-fade-enter
    .list-wrapper
      position: absolute
      left: 0
      bottom: 0
      width: 100%
      background-color: $color-highlight-background
      .list-header
        position: relative
        padding: 20px 30px 10px 20px
        .title
          display: flex
          align-items: center
          .icon
            margin-right: 10px
            font-size: 30px
            color: $color-theme-d
          .text
            flex: 1
            font-size: $font-size-medium
            color: $color-text-l
          .clear
            extend-click()
            .icon-clear
              font-size: $font-size-medium
              color: $color-text-d
      .list-content
        max-height: 240px
        overflow: hidden
        .item
          display: flex
          align-items: center
          height: 40px
          padding: 0 30px 0 20px
          overflow: hidden
          &.list-enter-active, &.list-leave-active
            transition: all 0.1s
          &.list-enter, &.list-leave-to
            height: 0
          .current
            flex: 0 0 20px
            width: 20px
            font-size: $font-size-small
            color: $color-theme-d
          .text
            flex: 1
            no-wrap()
            font-size: $font-size-medium
            color: $color-text-d
          .like
            extend-click()
            margin-right: 15px
            font-size: $font-size-small
            color: $color-theme
            .icon-favorite
              color: $color-sub-theme
          .delete
            extend-click()
            font-size: $font-size-small
            color: $color-theme
      .list-operate
        width: 140px
        margin: 20px auto 30px auto
        .add
          display: flex
          align-items: center
          padding: 8px 16px
          border: 1px solid $color-text-l
          border-radius: 100px
          color: $color-text-l
          .icon-add
            margin-right: 5px
            font-size: $font-size-small-s
          .text
            font-size: $font-size-small
      .list-close
        text-align: center
        line-height: 50px
        background: $color-background
        font-size: $font-size-medium-x
        color: $color-text-l
</style>



